.sidebar {
  height: 100vh;
  background-color: #f5f1ea; /* tono nude/beige */
  position: fixed;
  top: 0;
  left: 0;
  transition: width 0.3s ease;
  padding: 20px 10px;
  box-shadow: 2px 0 5px rgba(0,0,0,0.1);
  overflow: hidden;
  width: 40px; /* estado cerrado por defecto */
  z-index: 1000;
  
  /* Borde superior e inferior redondeado */
  border-top-right-radius: 20px;
  border-bottom-right-radius: 20px;
  border: solid 1px red;
}

.sidebar.open {
  left: 0;
  width: 150px;
}

.hamburger-container {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100px;
}

.sidebar.open .hamburger-container {
  justify-content: flex-end;
  padding-right: 10px;
}

.hamburger-btn {
  background-color: #d9cfc3;
  border: none;
  font-size: 1rem; /* Tama침o m치s peque침o */
  color: white;    /* Rayas blancas */
  cursor: pointer;
  padding: 6px 10px; /* M치s compacto */
  border-radius: 5px;
  margin-bottom: 20px;
}

.sidebar a {
  color: #4a3f35;
  text-decoration: none;
  font-weight: bold;
  display: block;
  margin-bottom: 10px;
}

.sidebar a:hover {
  text-decoration: underline;
}

.sidebar ul {
  list-style: none;
  padding-left: 0px;
  margin: 10px;
}